<template>
  <div id="app">
    
    <div class="image">
      <img class="loading" v-if="$store.state.loading" src="./assets/loader1.gif"/>
      <img crossorigin="anonymous" class="profile_pic" :src="$store.state.linkUrl" />
    </div>
    <form @submit="search">

    <label for="user"> Enter your Instagram handle. </label>
      <div class="input_wrapper">
        <p class="user_label"> @ </p>
        <input type="text" class="user" id="user" ref="text" @input="handleChange"/>
      </div>
      <br/>
      <input type="submit" :class="`submit ${inactive ? `inactive` : ``}`" v-if="!$store.state.loading"/>
      <div v-else>Loading..</div>
    </form>
    <VueRecaptcha
      :sitekey="siteKey"
      @verify="captchaVerified = true">
    </VueRecaptcha>
  </div>
 
</template>

<script>
import { VueRecaptcha } from 'vue-recaptcha';
import {mapState} from 'vuex';
export default {
  name: 'App',
  components: {
    VueRecaptcha
  },
  watch: {
    linkUrl() {
      this.captchaVerified = false;
    }
  },
  computed: {
    ...mapState(['linkUrl'])
  },
  data() {
    return {
      loading: false,
      siteKey: "6Lct3GshAAAAAPDx8i8a7qFQVLwGDmeXSvLPaPSn",
      inactive: true,
      captchaVerified: false
    }
  },
  methods: {
    handleChange() {
      if (this.$refs.text.value.length > 0 && this.captchaVerified) {
        this.inactive = false;
        return;
      }
      this.inactive = true;
    },
    search(e) {
      e.preventDefault();
      if (this.loading || this.$refs.text.value.length === 0 || !this.captchaVerified) return;
      this.$store.commit("updateLoading", true);
      this.$api.profile_pic(this.$refs.text.value);
    }
  },
  mounted() {
  // console.log(this.$store.state.linkUrl);

  }
}
</script>

<style>
#app {
  font-family: Arial;
  text-align: center;
  max-width: 300px;
}
.image {
  width: 200px;
  height: 200px;
  background: grey;
  border-radius: 100px;
  overflow: hidden;
  margin: auto;
  margin-bottom: 20px;
}
.profile_pic, .loading {
  width: 100%;
  height: 100%;
}
.loading {
  z-index: 2;
}
.user {
  border: 1px solid #c6c6c6;
  padding: 10px 10px 10px 30px;
  border-radius: 10px;
  width: calc(100% - 40px);
}
.user:focus {
  outline: none;
}
.submit {
  border: none;
  background: linear-gradient(90deg, rgba(113,84,192,1) 0%, rgba(0,151,236,1) 100%);
  padding: 10px;
  color:white;
  border-radius: 10px;
  width: 100%;
  margin: 10px 0;
  cursor: pointer;
  transition: all .25s ease;
}
.inactive {
  background: grey;
  transition: all .25s ease;
  cursor: default;
}
.user_label {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 10px;
  margin: 0;
}
.input_wrapper {
  position: relative;
}
</style>
