/* eslint-disable no-unused-vars */
import { store } from './store';
const api = "keyaJEowLnEfKAamU";
var tables = "tblSUNsZFXUQ8DS75";
const { default: axios } = require( 'axios' );
const baseId = "appYUpR8L67hyxGt6";
const url = `https://api.airtable.com/v0/${baseId}/${tables}?api_key=${api}`;

var Airtable = require('airtable');
var base = new Airtable({apiKey: api}).base(baseId);


export default new class API {
    constructor() {
        this.records;
        this.apiCounter = 0;
        this.options = [];
        this.init();
     }

    init() {
        axios.get(url).then((res) => {
            this.records = res.data.records;
        })        
    }

    async profile_pic(username) {
        return new Promise((profile_pic_resolve) => {
            this.init();
            var res = this.records.filter(i => i.fields.Username === username);
            if (res.length > 0) {

                // If profile already exists in Airtable
                if (res[0].fields.Profile_Picture) {
                    store.commit("updateLinkUrl", res[0].fields.Profile_Picture[0].url);
                    store.commit("updateLoading", false);
                }
                // If profile doesn't exist in Airtable, call API to fetch profile pic and upload into Airtable
                } else {

                    
                    this.options = [
                        {
                            // 10 / day https://rapidapi.com/v.sobolev/api/Instagram/
                            method: 'GET',
                            url: `https://instagramdimashirokovv1.p.rapidapi.com/user/${username}`,
                            headers: {
                              'X-RapidAPI-Key': '6d50373928msh8ea7650abbc2053p1c1465jsn8d11a1823388',
                              'X-RapidAPI-Host': 'InstagramdimashirokovV1.p.rapidapi.com'
                            },
                            id: "v.sobolev"
                        },
                        {
                            // 30 / day
                            method: 'GET',
                            url: `"https://instagram-fast.p.rapidapi.com/profile/"${username}`,
                            headers: {
                              'X-RapidAPI-Key': '6d50373928msh8ea7650abbc2053p1c1465jsn8d11a1823388',
                              'X-RapidAPI-Host': 'instagram-fast.p.rapidapi.com'
                            },
                            id: "instagram-fast"
                        },
                        {
                            // 20 / day https://rapidapi.com/JoTucker/api/instagram-scraper2/
                            method: 'GET',
                            url: 'https://instagram-scraper2.p.rapidapi.com/user_info' ,
                            params: {user_name: username},
                            headers: {
                              'X-RapidAPI-Key': '6d50373928msh8ea7650abbc2053p1c1465jsn8d11a1823388',
                              'X-RapidAPI-Host': 'instagram-scraper2.p.rapidapi.com'
                            },
                            id: "instagram-scraper2"
                        },   
                        {
                            // 50/month https://rapidapi.com/fariswdcash-434L2ELS8qx/api/instagram-profile1/
                            method: 'GET',
                            url: `https://instagram-profile1.p.rapidapi.com/getprofile/${username}`,
                            headers: {
                                'X-RapidAPI-Key': '6d50373928msh8ea7650abbc2053p1c1465jsn8d11a1823388',
                                'X-RapidAPI-Host': 'instagram-profile1.p.rapidapi.com'
                            },
                            id: "instagram-profile1"
                        },
                        {
                            // 200 / month https://rapidapi.com/iq.faceok/api/instagram-looter2/
                            method: 'GET',
                            url: 'https://instagram-looter2.p.rapidapi.com/profile',
                            params: {username: username},
                            headers: {
                              'X-RapidAPI-Key': '6d50373928msh8ea7650abbc2053p1c1465jsn8d11a1823388',
                              'X-RapidAPI-Host': 'instagram-looter2.p.rapidapi.com'
                            },
                            id: "instagram-looter2"
                        },
                        {
                            // 100/month https://rapidapi.com/arraybobo/api/instagram-scraper-2022/
                            method: 'GET',
                            url: 'https://instagram-scraper-2022.p.rapidapi.com/ig/info_username/',
                            params: {user: 'sanembahcekapili'},
                            headers: {
                              'X-RapidAPI-Key': '6d50373928msh8ea7650abbc2053p1c1465jsn8d11a1823388',
                              'X-RapidAPI-Host': 'instagram-scraper-2022.p.rapidapi.com'
                            },
                            id: "instagram-scraper-2022"
                        },
                        {
                            // 100 / month, https://rapidapi.com/yagamicell/api/instagram-data10/
                            method: 'GET',
                            url: `https://instagram-data10.p.rapidapi.com/api/rapid_api/instagram_profile/${username}`,
                            headers: {
                                id: username,
                                'X-RapidAPI-Key': '6d50373928msh8ea7650abbc2053p1c1465jsn8d11a1823388' ,
                                'X-RapidAPI-Host': 'instagram-data10.p.rapidapi.com'
                            },
                            id: "instagram-data10"
                        },
                        {
                            // 100 / month, https://rapidapi.com/omarcosramiro/api/instagram-scrapper-bulk/
                            method: 'GET',
                            url: `https://instagram-scrapper-bulk.p.rapidapi.com/profile/${username}`,
                            headers: {
                              'X-RapidAPI-Key': '6d50373928msh8ea7650abbc2053p1c1465jsn8d11a1823388' ,
                              'X-RapidAPI-Host': 'instagram-scrapper-bulk.p.rapidapi.com'
                            },
                            id: "instagram-scrapper-bulk"
                        }                     

                    ];
                      
                      
                    axios.request(this.options[this.apiCounter]).then((res) => {

                        this.processUrl(this.options[this.apiCounter].id, res)
                        .then((profilepicurl) => this.uploadToAirtable(username, profilepicurl)
                        .then((records) => this.findInAirtable(records)
                        .then((found) => {
                            store.commit("updateLinkUrl",   found.fields.Profile_Picture[0].url);
                            store.commit("updateLoading", false);

                        })))

                    }, () => {
                        console.log("error! ", this.apiCounter);
                        this.apiCounter += 1;
                        if (this.apiCounter <= this.options.length - 1) this.profile_pic(username);
                        // else return;

                    })
                }
            })
        
    }

    processUrl(id, res) {
        return new Promise((profilepicurl) => {
            switch(id) {
                case "instagram-fast": {
                    profilepicurl(res.data.data.user.profile_pic_url);
                    break;
                }
                case "instagram-scraper2": {
                    profilepicurl(res.data.data.user.profile_pic_url);
                    break;
                }
                case "instagram-profile1": {
                    profilepicurl(res.data.profile_pic_url);
                    break;
                }
                case "instagram-data10": {
                    profilepicurl(res.data.data.profile_picture);
                    break;
                }
                case "instagram-scrapper-bulk": {
                    profilepicurl(res.data.data.user.profile_pic_url);
                    break;
                }
                case "v.sobolev": {
                    profilepicurl(res.data.profile_pic_url);
                    break
                }
               case "instagram-scraper-2022": {
                    profilepicurl(res.data.user.profile_pic_url);
                    break
                }
                case "instagram-looter2": {
                    profilepicurl(res.data.profile_pic_url);
                    break
                }
            }
        })
        
    }

    uploadToAirtable(username, picUrl) {
        return new Promise((resolve) => [
            base('Table 1').create([
                {
                    "fields": {
                    "Username": username,
                    "Profile_Picture": [{"url": picUrl}]
                }
            }],(err, records) => {
                setTimeout(() => {
                    resolve(records); // or res, gotta see which one
                }, 10000);
            })
        ]);
    }

    findInAirtable(id) {
        return new Promise((foundBase, error) => {
            base('Table 1').find(id[0].id).then((res) => {
                foundBase(res);
            }, (err) => {
                console.warn(err);
                error(err);
            })
        })
        
    }

    async uploadProfilePic(id, picUrl) {
        return new Promise((resolve) => {
            base('Table 1').update([
                {
                "id": id,
                "fields": {
                    "Profile_Picture": [{url: picUrl}]
                }
            }]);
            resolve(picUrl);
        })
    }
}
