import { createStore } from 'vuex';

export const store = createStore({
    state () {
      return {
        linkUrl: '',
        loading: false
      }
    },
    mutations: {
        updateLinkUrl (state, url) {
          state.linkUrl = url;
        },
        updateLoading (state, loading) {
            state.loading = loading;
          }
      }
  })